import {Loading} from 'quasar';
import cfg from "../heating-config";
import axios from 'axios';

require('url-search-params-polyfill');

export default Vue.extend({
  data() {
    return {
      modifyFor: null,
      month: null,
      date: null,
      serverPagination: {
        page: 1,
        rowsNumber: 50 // specifying this determines pagination is server-side
      },
      monthsOptions: [
        {
          label: 'JanuÃ¡r',
          value: '1'
        },
        {
          label: 'FebruÃ¡r',
          value: '2'
        },
        {
          label: 'Marec',
          value: '3'
        },
        {
          label: 'AprÃ­l',
          value: '4'
        },
        {
          label: 'MÃ¡j',
          value: '5'
        },
        {
          label: 'JÃºn',
          value: '6'
        },
        {
          label: 'JÃºl',
          value: '7'
        },
        {
          label: 'August',
          value: '8'
        },
        {
          label: 'September',
          value: '9'
        },
        {
          label: 'OktÃ³ber',
          value: '10'
        },
        {
          label: 'November',
          value: '11'
        },
        {
          label: 'December',
          value: '12'
        }
      ],
      selectOptions: [
        {
          label: 'CelÃ½ mesiac',
          value: 'month'
        },
        {
          label: 'DeÅ',
          value: 'day'
        }
      ],
      tableData: [],
      inputPins: [],
      pagination: {
        sortBy: null, // String, column "name" property value
        descending: false,
        page: 1,
        rowsPerPage: 10 // current rows per page being displayed
      },
      rowSelected: [],
      columns: [{
        name: 'hour',
        required: true,
        label: 'Hodina',
        align: 'left',
        field: 'hour',
        sortable: true,
      }, {
        name: 'minute',
        required: true,
        label: 'MinÃºta',
        align: 'left',
        field: 'minute',
        sortable: true,
      }, {
        name: 'ignore',
        required: true,
        label: 'IgnorovaÅ¥ jas',
        align: 'left',
        field: 'ignore',
        sortable: true,
      }, {
        name: 'hor',
        required: true,
        label: 'HorizontÃ¡lne',
        align: 'left',
        field: 'hor',
        sortable: true,
      }, {
        name: 'vert',
        required: true,
        label: 'VertikÃ¡lne',
        align: 'left',
        field: 'vert',
        sortable: true,
      }]
    }
  },
  methods: {
    loadCurrentState() {
      Loading.show();
      let url = cfg.BASE_URL + "solar";
      if (this.modifyFor === 'day' && this.date !== null)
        url = url + '?date=' + this.date.slice(0, 10);
      else
        url = url + '?month=' + this.month;
      axios.get(url)
          .then(response => {
            this.tableData = response.data;
            Loading.hide();
          })
          .catch(error => {
            Loading.hide();
            alert(error)
          });
    },
    editSolar(row: any) {
      let url = cfg.BASE_URL + "solar";
      if (this.modifyFor === 'day' && this.date !== null)
        url = url + '?date=' + this.date.slice(0, 10);
      else
        url = url + '?month=' + this.month;
      Loading.show();
      axios.put(cfg.BASE_URL + "solar/cmd/update", row, {method: "PUT"})
          .then(response => {
            this.loadCurrentState();
            Loading.hide();
          })
          .catch(error => {
            Loading.hide();
            alert(error)
          });
    },
    deleteSolar() {
      let url = cfg.BASE_URL + "solar";
      if (this.modifyFor === 'day' && this.date !== null)
        url = url + '?date=' + this.date.slice(0, 10);
      else
        url = url + '?month=' + this.month;
      Loading.show();
      axios.post(cfg.BASE_URL + "solar/cmd/delete", this.rowSelected, {method: "POST"})
          .then(response => {
            this.loadCurrentState();
            Loading.hide();
          })
          .catch(error => {
            Loading.hide();
            alert(error)
          });
    }
  },
  mounted(): void {
  }
});
