








































































import {Vue} from 'vue-property-decorator';
import {Loading} from 'quasar';
import cfg from "../heating-config";
import axios from 'axios';


export default Vue.extend({
  data() {
    return {
      name: "",
      refCd: "",
      deviceId: "",
      selected: [],
      freeDeviceIds: [],
      addPopupDisplayed: false,
      tempSeries: [],
      options: {
        chart: {
          type: 'line',
          stacked: false,
          height: 350,
          zoom: {
            type: 'x',
            enabled: true,
            autoScaleYaxis: true
          },
          toolbar: {
            autoSelected: 'zoom'
          }
        },
        dataLabels: {
          enabled: false
        },
        markers: {
          size: 0,
        },
        title: {
          text: 'Teplota',
          align: 'left'
        },
        yaxis: {
          type: 'number',
          title: {
            text: 'Teplota'
          },
        },
        xaxis: {
          type: 'datetime',
          labels: {
            format: 'dd. MM. yyyy'
          },
          minHeight: 120,
          maxHeight: 120
        },
        legend: {
          position: 'top'
        },
        tooltip: {
          x: {
            show: true,
            format: 'dd. MM. yyyy HH:mm'
          },
        }
      },
      fromDate: new Date(),
      toDate: new Date(),
      columns: [
        {
          name: 'name',
          label: 'Pomenovanie meracieho miesta',
          align: 'left',
          field: 'name',
          sortable: true
        },
        {
          name: 'refCd',
          label: 'UnikÃ¡tny kÄ¾ÃºÄ',
          align: 'left',
          field: 'refCd',
          sortable: true
        }, {
          name: 'deviceId',
          label: 'ID zariadenia',
          align: 'left',
          field: 'deviceId',
          sortable: true
        }
      ],
      data: []
    }
  },
  methods: {
    loadCurrentState() {
      Loading.show();
      axios.get(cfg.BASE_URL + "temp/measurePlace")
          .then(response => {
            this.data = response.data;
            Loading.hide();
          })
          .catch(error => {
            Loading.hide();
            alert(error)
          });
      axios.get(cfg.BASE_URL + "temp/freeDeviceIds")
          .then(response => {
            this.freeDeviceIds = response.data;
            Loading.hide();
          })
          .catch(error => {
            Loading.hide();
            alert(error)
          });
    },
    saveMeasureplace() {
      let data: any = {};
      data['name'] = this.name;
      data['deviceId'] = this.deviceId;
      axios.post(cfg.BASE_URL + "temp/measurePlace", data, {method: "post"})
          .then(response => {
            this.loadCurrentState();
          })
          .catch(error => {
            alert(error)
          });
    },
    editMeasureplace(row: any) {
      axios.put(cfg.BASE_URL + "temp/measurePlace/" + row.refCd, row, {method: "PUT"})
          .then(response => {
            this.loadCurrentState();
          })
          .catch(error => {
            alert(error)
          });
    },
    deleteMeasurePlace() {
      axios.delete(cfg.BASE_URL + "temp/measurePlace/" + this.selected[0].refCd)
          .then(response => {
            this.loadCurrentState();
          })
          .catch(error => {
            alert(error)
          });
    }
  },
  mounted(): void {
    let date = new Date();
    date.setDate(this.fromDate.getDate() - 7);
    this.fromDate = date;
    this.loadCurrentState();
  }
});
